const galleryList = document.querySelector('.gallery');
for (const { preview, original, description } of images) {
  const newLI = document.createElement('li');
  newLI.classList.add('gallery-item');
  galleryList.prepend(newLI);
  const link = document.createElement('a');
  link.classList.add('gallery-link');
  newLI.append(link);
  link.setAttribute('href', original);
  const imgItem = document.createElement('img');
  imgItem.classList.add('gallery-image');
  link.append(imgItem);
  imgItem.setAttribute('src', preview);
  imgItem.setAttribute('alt', description);
  imgItem.setAttribute('data-source', original);
  imgItem.style.width = '360px';
  imgItem.style.height = '200px';
}
var lightbox = new SimpleLightbox('.gallery a', {
  captionsData: 'alt',
  captionDelay: 250,
});
